<template>
  <div class="mvList">
    <Loading v-if="isLoading"></Loading>
    <div class="box">
      <div
        class="item"
        v-for="(item , index) in data"
        :key="index"
        @click="jump(item.id,index)"
      >
        <div class="bgShade">
          <img :src="item.cover" alt="" />
        </div>
        <div class="top">
          <div class="mvIcon">MV</div>
          <div class="banner">
            <div class="borde">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
            <img :src="item.cover" alt="" />
          </div>
          <div class="bom">
            <!-- <img :src="item.picUrl" alt="" /> -->
            <!-- <span v-if="!item.duration">00:00</span> -->
            <span v-if="item.duration">
              {{ Math.floor(item.duration / 1000 / 60) > 9 ? "" : "0"
              }}{{ Math.floor(item.duration / 1000 / 60) }}:
              {{
                Math.ceil(((item.duration % (3600 * 24)) % 3600) / 60) > 9
                  ? ""
                  : "0"
              }}{{
                Math.ceil(((item.duration % (3600 * 24)) % 3600) / 60)
              }}</span
            >
          </div>
        </div>
        <div class="bottom">
          <p>{{ item.name }}</p>
          <div>
            <span>{{ item.playCount }} 次</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.loading {
  margin-top: 200px;
}
.box {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  flex-wrap: wrap;
  padding-bottom: 140px;
  margin-top: 70px;
  .item {
    width: 44%;
    height: 34vh;
    background-color: rgb(66, 55, 55);
    margin: 8px;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    .bgShade {
      height: 100%;
      // overflow: hidden;
      filter: blur(8px);
      // position: relative;
      img {
        height: 74%;
        left: 50%;
        position: absolute;
        transform: translateX(-50%);
      }
    }
    .top {
      width: 100%;
      height: 74%;
      position: absolute;
      top: 0;
      //   background-color: red;
      left: 0;
      .mvIcon {
        display: inline-block;
        background-color: rgba(179, 179, 179, 0.431);
        position: absolute;
        top: 10%;
        left: 50%;
        transform: translateX(-50%);
        padding: 1px 6px;
        border-radius: 20px;
        color: rgba(255, 255, 255, 0.534);
        font-weight: 600;
      }
      .banner {
        width: 90%;
        height: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        // background-color: aqua;
        transform: translate(-50%, -50%);
        border-radius: 8px;
        // overflow: hidden;
        .borde {
          width: 100%;
          height: 100%;
          background-color: rgb(5, 4, 4);
          border-radius: 6px;
          // overflow: hidden;
          background-size: cover;
          // filter: blur(5px);
          span {
            width: 20px;
            height: 20px;
            position: absolute;
            background-color: white;
            z-index: -1;
            &:nth-child(1) {
              top: -1px;
              left: -1px;
              border-radius: 4px;
            }
            &:nth-child(2) {
              top: -1px;
              right: -1px;
              border-radius: 4px;
            }
            &:nth-child(3) {
              bottom: -1px;
              left: -1px;
              border-radius: 4px;
            }
            &:nth-child(4) {
              bottom: -1px;
              right: -1px;
              border-radius: 4px;
            }
          }
        }
        img {
          position: absolute;
          top: 0;
          left: 50%;
          // width: 100%;
          height: 100%;
          border-radius: 6px;
          transform: translateX(-50%);
        }
      }
      .bom {
        width: 94%;
        // height: 20px;
        position: absolute;
        bottom: 0;
        padding: 0 3%;
        margin-bottom: 6px;
        img {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          float: left;
          border: 1px solid white;
        }
        span {
          float: right;
          line-height: 34px;
          font-size: 0.8rem;
          color: rgb(158, 158, 158);
        }
      }
    }
    .bottom {
      width: 100%;
      height: 26%;
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: white;
      padding: 3%;
      box-sizing: border-box;
      p {
        width: 100%;
        height: 38px;
        text-align: left;
        word-wrap: break-word;
        overflow: hidden;
        font-size: 0.9rem;
        font-weight: initial;
      }
      div {
        width: 94%;
        padding: 0 3%;
        // height: 20px;
        padding: 2px 0;
        text-align: left;
        font-size: 0.8rem;
        span {
          position: relative;
          padding-left: 18px;
          color: rgb(158, 158, 158);
          margin-left: 2px;
          &::before {
            content: "";
            width: 18px;
            height: 18px;
            position: absolute;
            left: 0;
            background-image: url(../assets/images/icon/playcountIcon.png);
            background-size: cover;
          }
        }
      }
    }
  }
}
</style>
<script>
import Loading from "@/components/Loading";
export default {
  name: "MVList",
  components: {
    Loading,
  },
  props: ["isIndex"],
  data() {
    return {
      data: [],
      isLoading: true,
      index: 0,
      urlArr: [
        "/mv/all?area=全部",
        "/mv/first?limit=10",
        "/mv/exclusive/rcmd?limit=10",
      ],
    };
  },
  watch: {
    isIndex: function(val) {
      this.isLoading=true;
      this.data=[]
      this.index = val-1;
      this.$axios
        .get(this.urlArr[this.index])
        .then((data) => {
          return data.data.data;
        })
        .then((item) => {
          this.data = item;
          this.isLoading = false;
        });
    },
  },
  created() {
    this.$axios
      .get(this.urlArr[this.index])
      .then((data) => {
        return data.data.data;
      })
      .then((item) => {
        this.data = item;
        this.isLoading = false;
      });
  },
  methods: {
    jump(id,index) {
      // console.log(222222222222);
      // console.log(index);
      this.$router.push({
        path: "/MV",
        name: "MV",
        query: {
          id: id,
          index:index,
          data: this.data,
        },
      });
    },
  },
};
</script>
